import React from 'react'
export default function Hello(props) {
  
  return (
    props.alert &&  <>
                <div className={`alert alert-${props.alert.type} alert-dismissible fade show`} id='alert' role="alert">
                    <strong>{props.alert.type}</strong>{props.alert.msg}
                 
                </div>
            </>
        )
    }
